import { mapState } from "vuex";
import { Toast } from "mint-ui";
import vCell from "@/components/v-cell";
import vNodata from "@/components/v-nodata";
const qs = require("qs");
export default {
  data() {
    return {
      hasGold: 0,
      applyGold: "",
      choseCardData: null
    };
  },
  components: {
    "v-nodata": vNodata,
    "v-cell": vCell
  },
  computed: {
    ...mapState(["token"])
  },
  created() {
    // 判断是否有缓存记录已选择的银行卡
    this.ifChoseCard();
    // 获取用户的分红金额
    this.goldAmount();
  },
  methods: {
    // 判断是否有缓存记录已选择的银行卡
    ifChoseCard() {
      const choseCard = window.localStorage.getItem("choseCard");
      if (!!choseCard) {
        this.choseCardData = JSON.parse(choseCard);
      } else {
        this.choseCardData = null;
      }
    },
    // 申请提现
    applyGetGold() {
      const hasGold = this.hasGold;
      const applyGold = this.applyGold;
      const choseCardData = this.choseCardData;
      // 检验数据先
      if (hasGold == 0) {
        this.showTip("您的金额为0，不能提现");
        return;
      }
      if (applyGold === "") {
        this.showTip("请输入提现金额数");
        return;
      }
      if (applyGold > hasGold) {
        this.showTip("提现额度超过您现有的金额");
        return;
      }
      if (applyGold < 100) {
        this.showTip("每次提现金额至少100元");
        return;
      }
      if (!choseCardData) {
        this.showTip("请选择一张提现银行卡");
        return;
      }
      // 校验通过后，开始读取提交接口
      const loading = Toast({
        message: "提交中...",
        iconClass: "loading",
        duration: 30000
      });
      this.$axios
        .post(
          this.api.applyGetGold,
          qs.stringify({
            bank_card_id: choseCardData.cardId,
            take_cash_amount: applyGold
          }),
          {
            headers: {
              "content-type": "application/x-www-form-urlencoded",
              "Authorization": this.token
            }
          }
        )
        .then(res => {
          loading.close();
          const resData = res.data;
          if (resData.code !== 1) {
            this.showTip(resData.message);
            return;
          }
          // 绑定成功后清空数据，刷新所剩金额
          this.showTip("提现申请成功！", 1500, "ok");
          this.applyGold = "";
          this.goldAmount();
        })
        .catch(res => {
          loading.close();
          this.showTip("提现失败，请重试！");
        });
    },
    // 获取用户的分红金额
    goldAmount() {
      this.$axios
        .get(this.api.goldAmount, { headers: { "Authorization": this.token } })
        .then(res => {
          const resData = res.data;
          if (resData.code !== 1) {
            this.showTip(resData.message);
            this.hasGold = 0;
            return;
          }
          this.hasGold = resData.content;
        })
        .catch(res => {
          this.showTip("未能获取到您的分红金额");
          this.hasGold = 0;
        });
    }
  }
};



